<template>
  <div class="pa-4">
    <portal to="app-header">
      <router-link
        to="/throttle"
        class="ml-2"
      >
        参考节流
      </router-link>
      <router-link
        to="/debounce"
        class="ml-2"
      >
        看看示例
      </router-link>
    </portal>
    <div class="d-flex">
      <h1>Debounce</h1>
      <span>{{ msg }}</span>
    </div>
    <p><code>debounce(func, wait)</code>自最近一次触发后延迟 wait 毫秒调用 func</p>
    <CodeHighlight :code="code" />
    <v-btn @click="handleClick">
      运行
    </v-btn>
  </div>
</template>

<script>
import CodeHighlight from '@/modules/codehl/CodeHighlight.vue';

function debounce(func, wait) {
  let timer = null;

  return function () {
    if (timer) {
      clearTimeout(timer);
      timer = null;
    }

    let self = this;
    let args = arguments;

    timer = setTimeout(function () {
      func.apply(self, args);
      timer = null;
    }, wait);
  };
}

export default {
  components: {
    CodeHighlight,
  },
  data() {
    return {
      msg: '',
      code: `function debounce(func, wait) {
  let timer = null;

  return function () {
    if (timer) {
      clearTimeout(timer);
      timer = null;
    }

    let self = this;
    let args = arguments;

    timer = setTimeout(function () {
      func.apply(self, args);
      timer = null;
    }, wait);
  };
}`,
run: debounce(function () {
  console.log("防抖函数执行！");
  this.msg = "完成";
}, 500),
    };
  },
  mounted() {
  },
  methods: {
    handleClick() {
      this.msg = "运行中...";
      this.run()
    }
  },
};
</script>

<style scoped>
.v-btn {
  margin-right: 10px;
}
</style>